}

.nav-menu {
  justify-content: space-around;
  align-items: center;
  gap: 3rem;
  display: flex;
}

.nav-toggler div {
  background: #a9a9a9;
  width: 3rem;
  height: .2rem;
  margin: .4rem;
  transition: all 2s ease-in;
}

.nav-toggler {
  cursor: pointer;
  display: none;
}

@media screen and (width <= 780px) {
  .nav-toggler {
    cursor: pointer;
    display: block;
  }

  .nav-menu {
    background: #001c49;
    flex-direction: column;
    width: 50%;
    height: 93vh;
    padding: 50px;
    transition: all .5s ease-in;
    position: fixed;
    top: 11vh;
    right: 0;
    transform: translateX(100%);
  }

  .nav-menu:hover {
    background: #fff;
  }

  a:hover {
    color: #000;
    background: gray;
    font-weight: 800;
    transition: all .5s ease-in;
  }
}

.nav__active {
  transform: translate(0%);
}

.toggle .line1 {
  transform: rotate(-45deg)translate(-4px, 5px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  transform: rotate(45deg)translate(-4px, -5px);
}

