
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* overflow: hidden; */
    overflow-x: hidden;
}
html{
    font-size: 62%;
}
body{
    font-size: 1.6rem;
    overflow-y: auto
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    color: darkgray;
}

.nav
{   
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 8vh;
    background-color: rgb(0,28,73);
    /* position: fixed; */
    width: 100vw;
    color: red;
    /* top: -6px; */

}
.nav-item{
    width: 10vw
}
.logo
{
    height: 30%;
    width: 40%;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.nav-menu
{
    display: flex;
    align-items: center;
    justify-content:space-around;
    gap: 3rem;
    
}
.nav-toggler div{
    width: 3rem;
    height: 0.2rem;
    margin: 0.4rem;
    background: darkgray;
    transition: 2s ease-in;
}
.nav-toggler{
    cursor: pointer;
    display: none;

}
@media screen and (max-width: 780px){
    .nav-toggler{
    display: block;
    cursor: pointer;
    }
    .nav-menu{
        position: fixed;
        top: 11vh;
        right:0vh;
        width: 50%;
        height: 93vh;
        padding: 50px;
        background: rgb(0,28,73);
        flex-direction: column;
        transform: translateX(100%);
        transition: 0.5s ease-in;
    }
    .nav-menu:hover {
        background: white;
    }
     a:hover
    {
        background: gray;
        color: black;
        font-weight: 800;
        transition: 0.5s ease-in;
    }
}
    
/* nav active class */
.nav__active{
    transform: translate(0%);
}
.toggle .line1 {
    transform: rotate(-45deg) translate(-4px, 5px);
  }
  .toggle .line2 {
    opacity: 0;
  }
  .toggle .line3 {
    transform: rotate(45deg) translate(-4px, -5px);
  }

